.form {
  display: flex;
  justify-content: space-between;
}

.label {
  font-style: italic;
}

.input {
  margin-top: 5px;
}

.btn {
  display: inline-flex;
  padding: 8px 12px;
  border-radius: 4px;
  border: none;
  font: inherit;
  cursor: pointer;
  background-color: #e2e5e8;
  color: inherit;
}

.btn:hover {
  background-color: #1976d2;
  color: #fff;
}
